import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { OpportunityListComponent } from './opportunitylist.component';
import { AddeditopportunityComponent } from './addeditopportunity.component';
import { OpportunityviewComponent } from './opportunityview.component';
import { OpportunityViewNewComponent } from './opportunityviewNew.component';


const routes: Routes = [
  { path: '', component: OpportunityListComponent },
  { path: 'addOpportunity', component: AddeditopportunityComponent },
  { path: 'editOpportunity/:id', component: AddeditopportunityComponent },
  { path: 'view/:id', component: OpportunityviewComponent },
  { path: ':id', component: OpportunityListComponent },
  { path: 'viewOpportunity/:id', component: OpportunityViewNewComponent },
];  
  

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OpportunityListRoutingModule { }
